.dice {
  width: 200px;
  .space {
    width: 200px;
    height: 200px;
    perspective: 800px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .box {
    width: 100px;
    height: 100px;
    transform-style: preserve-3d;
    position: relative;
    &.animation {
      animation: rotation 1s linear 0s infinite normal none running;
    }
    &.top {
      transform: rotateX(-90deg) rotateY(0deg) rotateZ(0deg);
    }
    &.bottom {
      transform: rotateX(90deg) rotateY(0deg) rotateZ(0deg);
    }
    &.front {
      transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    &.back {
      transform: rotateX(180deg) rotateY(0deg) rotateZ(180deg);
    }
    &.left {
      transform: rotateX(90deg) rotateY(90deg) rotateZ(-90deg);
    }
    &.right {
      transform: rotateX(90deg) rotateY(-90deg) rotateZ(90deg);
    }
  }

  @keyframes rotation {
    0% {
      transform: rotateX(0deg) rotateY(-90deg) rotateZ(-180deg);
    }

    25% {
      transform: rotateX(90deg) rotateY(0deg) rotateZ(-90deg);
    }

    50% {
      transform: rotateX(180deg) rotateY(90deg) rotateZ(0deg);
    }

    75% {
      transform: rotateX(270deg) rotateY(180deg) rotateZ(90deg);
    }

    100% {
      transform: rotateX(360deg) rotateY(270deg) rotateZ(180deg);
    }
  }

  .face {
    text-align: center;
    width: 100%;
    height: 100%;
    border: solid 2px #000;
    box-sizing: border-box;
    background-color: #fff;
    font-size: 65px;
    margin: auto 0;
    position: absolute;
    &.top {
      transform: rotateX(90deg) translateZ(50px);
    }
    &.bottom {
      transform: rotateX(-90deg) translateZ(50px);
    }
    &.front {
      transform: translateZ(50px);
    }
    &.back {
      transform: rotateY(180deg) translateZ(50px);
    }
    &.right {
      transform: rotateY(90deg) translateZ(50px);
    }
    &.left {
      transform: rotateY(-90deg) translateZ(50px);
    }
  }

  .moto .face {
    background-color: #fff;
    color: #000;
  }
  .res .face {
    background-color: #000;
    color: #fff;
  }
}
